import React, {useEffect, useRef} from "react";
import style from "./ContactForm.module.scss"
import styleContainer from "./../common/styles/Container.module.css"
import {gsap} from "gsap";
import {ScrollTrigger} from 'gsap/ScrollTrigger';
import {BottomScg} from "../common/bottomSvg/BottomSvg";

export const ContactForm = () => {
    const content = useRef<HTMLDivElement>(null)
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger)
        gsap.fromTo(
            content.current, {
                opacity: 0,
                x: -400,
            }, {
                opacity: 1,
                x: 0,
                scrollTrigger: {
                    trigger: content.current!,
                    start: 'top center+=200',
                    toggleActions: 'play none none reverse'
                }
            });
    }, []);

    const svgBackColor = {
        fill: '#2a2a2a'
    };

    return (
        <div className={style.contactForm} id={'CONTACTS'}>
            <div ref={content} className={`${styleContainer.container} ${style.container}`}>
                <h2>Quick Contact Form</h2>
                <form className={style.form}>
                    <input placeholder='Name' id='id'/>
                    <input placeholder='Lastname' id='lastname'/>
                    <input placeholder='Email' id='email'/>
                    <textarea placeholder='Message' id='message'/>
                    <button className={style.submitBtn} type={"submit"}>Submit</button>
                </form>
            </div>
            <BottomScg style={svgBackColor}/>
        </div>
    )
}
